body {
  background-color: #000;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

.container {
  width: 310px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (width >= 640px) {
  .container {
    width: 640px;
  }
}

@media screen and (width >= 1246px) {
  .container {
    width: 1246px;
  }
}

.header {
  background-color: #000;
}

.header__sign {
  justify-content: end;
  gap: 15px;
  display: flex;
}

.header__logo {
  position: absolute;
}

.header__central {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.header__title {
  text-align: center;
  width: 588px;
  font-family: "Montserrat" sans-serif;
  color: #dc56c5;
  margin-top: 40px;
  margin-bottom: 52px;
  font-size: 50px;
  font-weight: 900;
}

.header__sign-in, .header__sign-up {
  text-align: center;
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  text-decoration: none;
}

.header__inputs {
  flex-direction: row;
  gap: 30px;
  display: flex;
}

.header__search, .header__country {
  text-align: center;
  color: #939393;
  backdrop-filter: blur(20px);
  background: #fff3;
  border: 1px solid gray;
  border-radius: 25px;
  width: 270px;
  height: 40px;
  font-size: 16px;
  font-weight: 400;
}

.header__btn {
  cursor: pointer;
  text-align: center;
  font-family: "Montserrat" sans-serif;
  text-transform: uppercase;
  color: #fff;
  background: #0006;
  border: 2px solid #dc56c5;
  border-radius: 25px;
  align-items: center;
  margin-bottom: 89px;
  padding: 4px 4px 4px 10px;
  font-size: 13px;
  font-weight: 500;
  transition: background .3s, color .3s, border-color .3s, padding .3s;
  display: flex;
}

.header__btn-text {
  margin: 0;
  transition: color .3s;
}

.header__circle {
  background-color: #fff;
  border-radius: 50px;
  width: 16px;
  height: 16px;
  transition: background-color .3s, margin .5s;
}

.header__btn-dark .header__circle {
  background-color: #fff;
  margin-left: 6px;
  margin-right: 0;
}

.header__btn-light {
  order: 1;
  padding-left: 4px;
  padding-right: 10px;
}

.header__btn-light .header__circle {
  background-color: #0e0e0e;
  order: -1;
  margin-left: 0;
  margin-right: 6px;
}

@media screen and (width <= 779px) {
  .header__logo {
    position: absolute;
  }

  .header__sign {
    display: none;
  }

  .header {
    background-color: #000;
  }

  .header__central {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    display: flex;
  }

  .header_title {
    text-align: center;
    width: 285px;
    font-family: "Montserrat" sans-serif;
    color: #dc56c5;
    margin-top: 40px;
    margin-bottom: -15px;
    font-size: 18px;
    font-weight: 900;
  }

  .header__sign-in, .header_sign-up {
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    text-decoration: none;
  }

  .header__inputs {
    flex-direction: column;
    gap: 20px;
    margin-bottom: 133px;
    display: flex;
  }

  .header__search, .header__country {
    text-align: center;
    color: #939393;
    border: 1px solid gray;
    border-radius: 25px;
    min-width: 270px;
    height: 40px;
    font-size: 16px;
    font-weight: 400;
  }

  .header__btn {
    cursor: pointer;
    text-align: center;
    width: 134px;
    height: 24px;
    font-family: "Montserrat" sans-serif;
    text-transform: uppercase;
    color: #fff;
    background: #0006;
    border: 2px solid #dc56c5;
    border-radius: 25px;
    margin-top: -140px;
    margin-bottom: 59px;
    font-size: 13px;
    font-weight: 500;
  }
}

body.light-theme {
  color: #000;
  background-color: #fff;
}

body.light-theme .header {
  background-image: none;
}

body.light-theme .header__title {
  color: #000;
}

body.light-theme .header__btn {
  color: #000;
  background: #fff6;
  border-color: #333;
}

body.light-theme .header__search, body.light-theme .header__country {
  color: #333;
  background: #0000001a;
  border: 1px solid #aaa;
}

body.light-theme .header__sign-in, body.light-theme .header__sign-up {
  color: #000;
}

.backdrop {
  opacity: 1;
  background-color: #0003;
  width: 100%;
  height: 100%;
  transition: opacity .5s cubic-bezier(.83, -.08, .32, 1) .25s, visibility .5s cubic-bezier(.83, -.08, .32, 1) .25s;
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.signIn {
  min-width: 528px;
  transition: opacity .5s cubic-bezier(.83, -.08, .32, 1) .25s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.hero {
  padding-bottom: 60px;
}

.hero__list {
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.hero__item {
  flex-direction: column;
  align-items: center;
  max-width: 128px;
  display: flex;
}

.hero__img-wrap {
  border-radius: 50px 0;
  justify-content: center;
  width: 128px;
  height: 161px;
  display: flex;
  overflow: hidden;
}

.hero__img-teg {
  display: flex;
}

.hero__name {
  text-align: center;
  color: #dc56c5;
  font-size: 14px;
  font-weight: 700;
}

.hero__date {
  text-align: center;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
}

.hero__place {
  color: #fff;
  font-size: 12px;
  font-weight: 600;
}

@media screen and (width >= 720px) {
  .hero__item {
    max-width: 180px;
  }

  .hero__name, .hero__date {
    font-size: 16px;
  }

  .hero__place {
    font-size: 14px;
  }
}

@media screen and (width >= 1200px) {
  .hero {
    padding-bottom: 80px;
  }
}

.footer {
  color: #fff;
  background-color: #000;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 120px;
  padding: 0 20px;
  position: relative;
}

.footer__content {
  text-align: center;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
  padding-bottom: 10px;
  display: flex;
}

.footer__text {
  color: #ffffff40;
  font-size: 12px;
}

.footer__heart {
  color: #f006;
}

.footer__link {
  color: #fff6;
  text-decoration: none;
}

.footer__link:hover {
  color: #fff;
  text-decoration: underline;
}

.back-to-top {
  cursor: pointer;
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  background-color: #0000;
  border: 3px solid #a259ff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  transition: border-color .3s, transform .3s;
  display: flex;
  position: fixed;
  bottom: 10px;
  right: 26px;
}

.back-to-top.visible {
  opacity: 1;
  visibility: visible;
}

.back-to-top:hover {
  border-color: #fff;
  transform: translateY(-4px);
}

.back-to-top svg {
  stroke: #a259ff;
  width: 40px;
  height: 40px;
}
